.main {
    position: relative;
}

.product {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    gap: 50px;
    grid-template-columns: 1fr 1fr;

    &__img {
        width: 265px;
        height: auto;
        margin: 0 auto;
    }
    &__card {
        display: flex;
        width: 320px;
        margin: 0 auto;
        flex-direction: column;
        justify-content: space-between;
        border: 1px solid #dfdfdf;
        border-radius: 20px;
        padding: 15px;
    }
}

.body {
    &__title {
        text-align: center;
    }
}
.details__wrapper {
    margin-bottom: 20px;
}
.items {
    font-family: "Merriweather", serif;
    background: #f2ede7;
    border-radius: 8px;
    width: 120px;
    display: flex;
    font-size: 18px;
    height: 30px;
    overflow: hidden;
    &__control {
        width: 40px;
        cursor: pointer;
        transition: 0.2s ease-in;
        text-align: center;
        &:hover {
            background: #eb5a1e;
            color: #fff;
        }
    }
    &__current {
        width: 40px;
        text-align: center;
    }
}
.price {
    text-align: center;
    &__item {
        padding: 10px 0;
        &__small {
            font-size: 1.5rem;
            font-weight: 500;
            font-family: "Merriweather", serif;
        }
    }
    &__weight {
        color: #6c757d !important;
        font-size: 80%;
        line-height: 1;
        padding: 10px 0;
    }
    &__currency {
        font-size: 1.5rem;
        font-weight: 600;
        line-height: 1;
        padding: 10px 0;
    }
}
.btn {
    background-color: #26b10a;
    width: 100%;
    padding: 15px;
    color: #fff;
    font-size: 18px;
    border: none;
    border-radius: 25px;
    transition: all 0.3s;
    cursor: pointer;
    &:hover {
        background-color: #027502;
        transform: scale(1.05, 1.05);
    }
}
.change__grid {
    position: absolute;
    top: -55px;
    right: 25px;
    width: 40px;
    cursor: pointer;
}
.grid3 {
    grid-template-columns: repeat(3, 1fr);
}
