@media (max-width: 900px) {
    .product__card__cart {
        flex-direction: column;
        .product__card__cart__img {
            margin: 0 0 15px 0;
        }
    }

    .details__wrapper__cart {
        flex-direction: column;
        text-align: center;
        .items__cart {
            margin: 0 0 10px 0;
        }
    }
    .price__cart {
        flex-direction: column;
        margin: 0;
    }
    .items__control {
        padding: 15px 0;
        width: 50px;
    }
    .items__current {
        padding: 14px;
        font-size: 18px;
        margin: 0 10px;
    }
    .info__cart__title {
        font-size: 18px;
        margin-bottom: 10px;
    }
    .price__cart__currency {
        margin: 15px 0 25px 0;
    }
}
@media screen and (max-width: 780px) {
    .product {
        grid-template-columns: 1fr;
    }
    .change__grid {
        display: none;
    }
    .nav {
        &__menu {
            display: none;
        }
    }
    nav .burger__menu__btn {
        display: block;
    }
}
@media (max-width: 600px) {
    .nav__header__menu {
        display: none;
    }
    .nav .wrapper {
        justify-content: end;
    }
}
