.wrapper {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}

.product__card__cart {
    display: flex;
    align-items: center;
    margin: 20px;
    &__img {
        width: 150px;
        margin-right: 20px;
    }
}
.details__wrapper__cart {
    display: flex;
    align-items: center;
}
.info__cart {
    width: 350px;
}
.items__cart {
    display: flex;
    margin-right: 20px;
}
.items__control {
    border-radius: 15px;
}
.price__cart {
    display: flex;
    margin: 0 20px;
    align-items: center;
    justify-content: space-between;
    width: 160px;
    &__currency {
        font-size: 20px;
        color: #26b10a;
    }
    &__delete {
        font-size: 20px;
        border-radius: 50%;
        background-color: #db3822;
        color: #fff;
        padding: 10px 15px;
        cursor: pointer;
        transition: all 0.3s;
        &:hover {
            background-color: #af1919;
        }
    }
}
.cart__total__price {
    margin: 25px 0 50px 0;
    font-size: 25px;
}
.btn__buy {
    max-width: 300px;
    margin-bottom: 200px;
}
