.header {
    height: 200px;
}
.nav {
    position: sticky;
    top: 0;
    z-index: 100;
    background: #26b10a;
    opacity: 0.7;
    padding: 15px;
    margin-bottom: 70px;
    .wrapper {
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        align-items: center;
    }
    &__header__menu {
        display: flex;
        align-items: center;
        flex-grow: 1;
        &__item {
            color: #ffe000;
            font-size: 18px;
        }
        &__logo {
            width: 40px;
            margin-right: 30px;
        }
    }
    &__menu {
        display: flex;
        align-items: center;
        justify-content: end;
        &__item {
            margin-left: 20px;
            &__link {
                font-size: 18px;
                color: #fff;
                transition: all 0.3s;
                &:hover {
                    color: #e4a51e;
                }
                &__count {
                    background: #e4a51e;
                    border-radius: 50%;
                    padding: 1px 6px;
                    color: #fff;
                }
            }
        }
    }
}
