.burger__menu__btn {
    width: 30px;
    height: 30px;
    z-index: 150;
    display: none;
    cursor: pointer;
    position: relative;
}
.burger__menu__btn span {
    width: 30px;
    height: 2px;
    position: absolute;
    top: 50%;
    // left: 50%;
    // transform: translate(-50%, -50%);
    background-color: #ffe000;
    transition: all 0.5s;
}
.burger__menu__btn span:nth-of-type(2) {
    top: calc(50% - 10px);
}
.burger__menu__btn span:nth-of-type(3) {
    top: calc(50% + 10px);
}

.burger {
    height: 100%;
    width: 70%;
    position: fixed;
    background: #c3ac04;
    top: 0;
    left: 0;
    transition: all 0.5s ease-in-out;
    transform: translate(-100%, 0);
    &__menu {
        margin: 110px 0 0 0;
        &__item {
            padding: 30px;
        }
        &__link {
            color: #fff;
            font-size: 22px;
            transition: all 0.3s;
            &:hover {
                color: #0bff18;
            }
        }
        &__logo {
            width: 75px;
        }
    }
    &__logo {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 30px;
        &__desc__text {
            color: #fff;
            font-size: 16px;
        }
    }
    &__count {
        background: #0bff18;
    }
}

.active {
    transform: translate(0, 0);
}
.active span:nth-of-type(1) {
    display: none;
}
.active span:nth-of-type(2) {
    top: 50%;
    transform: translate(0, 0) rotate(45deg);
}
.active span:nth-of-type(3) {
    top: 50%;
    transform: translate(0, 0) rotate(-45deg);
}
